.bi-block-control-icon {
  font-size: 1.25rem;
  flex: 0 0 auto;
}

.components-dropdown-menu__popover.popover-align-text-start .components-dropdown-menu__menu > button {
  text-align: left;
  text-align: start;
}

hr.gutestrap-block-clear-placeholder {
  color: grey;
}
hr.gutestrap-block-clear-placeholder::after {
  content: "\f314";
  font-family: dashicons;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 0 0.5em;
  background-color: white;
}

.wp-block[data-type="gutestrap/clear"] {
  clear: both !important;
}

.block-editor-block-list__block[data-title][data-type="gutestrap/custom-scss"] {
  font-size: initial;
  margin: auto;
  border: thin dashed lightgrey;
  padding: 0.5em;
  padding-left: 0;
  border-radius: 0.5em;
  background: whitesmoke;
  max-width: 120ch !important;
}
.block-editor-block-list__block[data-title][data-type="gutestrap/custom-scss"]::before {
  padding: 0 0 0.75em 1em;
}

.gutestrap-custom-scss-editor {
  font-size: initial;
}
.components-modal__frame .gutestrap-custom-scss-editor textarea,
.components-modal__frame .gutestrap-custom-scss-editor .CodeMirror {
  width: 80vw;
  height: 50vh;
  min-height: 10em;
  max-width: 120ch;
}